.header {
  z-index: 1000;
  background: linear-gradient(135deg, #667eea 0%, #764ba2 100%);
  justify-content: space-between;
  align-items: center;
  padding: 15px 40px;
  display: flex;
  position: sticky;
  top: 0;
  box-shadow: 0 4px 20px #0000001a;
}

.logo-container {
  align-items: center;
  gap: 15px;
  display: flex;
}

.logo {
  border: 3px solid #fff;
  border-radius: 50%;
  width: 70px;
  height: 70px;
  transition: transform .3s;
  box-shadow: 0 4px 15px #0003;
}

.logo:hover {
  transform: rotate(360deg)scale(1.1);
}

.logo-text {
  color: #fff;
  text-shadow: 2px 2px 4px #0000004d;
  letter-spacing: 1px;
  font-size: 28px;
  font-weight: bold;
}

.nav-items {
  padding: 0;
}

.nav-items > ul {
  gap: 10px;
  margin: 0;
  font-size: 16px;
  list-style-type: none;
  display: flex;
}

.nav-link {
  color: #fff;
  cursor: pointer;
  border-radius: 25px;
  align-items: center;
  gap: 8px;
  margin: 0;
  padding: 12px 20px;
  font-weight: 500;
  transition: all .3s;
  display: flex;
  position: relative;
}

.nav-link:hover {
  background-color: #fff3;
  transform: translateY(-2px);
}

.nav-icon {
  font-size: 20px;
}

.cart {
  background-color: #ff5722e6;
  padding: 12px 25px;
  position: relative;
}

.cart:hover {
  background-color: #ff5722;
  transform: translateY(-2px);
}

.cart-badge {
  color: #fff;
  background-color: #ff1744;
  border-radius: 50%;
  justify-content: center;
  align-items: center;
  width: 22px;
  height: 22px;
  font-size: 12px;
  font-weight: bold;
  animation: 2s infinite pulse;
  display: flex;
  position: absolute;
  top: 5px;
  right: 10px;
  box-shadow: 0 2px 8px #0000004d;
}

@keyframes pulse {
  0%, 100% {
    transform: scale(1);
  }

  50% {
    transform: scale(1.1);
  }
}

.body {
  background-color: #f5f7fa;
  min-height: calc(100vh - 200px);
  padding: 20px;
}

.search {
  background: #fff;
  border-radius: 15px;
  align-items: center;
  gap: 15px;
  max-width: 800px;
  margin-bottom: 20px;
  margin-left: auto;
  margin-right: auto;
  padding: 20px;
  display: flex;
  box-shadow: 0 2px 15px #00000014;
}

.search-input {
  border: 2px solid #e0e0e0;
  border-radius: 30px;
  outline: none;
  flex: 1;
  padding: 14px 25px;
  font-size: 16px;
  transition: all .3s;
}

.search-input:focus {
  border-color: #667eea;
  box-shadow: 0 0 0 3px #667eea1a;
}

.search-btn, .reset-btn {
  cursor: pointer;
  border: none;
  border-radius: 30px;
  padding: 14px 35px;
  font-size: 16px;
  font-weight: 600;
  transition: all .3s;
  box-shadow: 0 4px 15px #0000001a;
}

.search-btn {
  color: #fff;
  background: linear-gradient(135deg, #667eea 0%, #764ba2 100%);
}

.search-btn:hover {
  transform: translateY(-2px);
  box-shadow: 0 6px 20px #667eea66;
}

.reset-btn {
  color: #666;
  background-color: #f5f7fa;
  border: 2px solid #e0e0e0;
}

.reset-btn:hover {
  color: #667eea;
  background-color: #fff;
  border-color: #667eea;
}

.results-info {
  color: #666;
  text-align: center;
  margin-bottom: 20px;
  padding: 10px 20px;
  font-size: 16px;
  font-weight: 600;
}

.no-results {
  text-align: center;
  color: #999;
  padding: 80px 20px;
}

.no-results h3 {
  color: #666;
  margin-bottom: 10px;
  font-size: 28px;
}

.no-results p {
  font-size: 16px;
}

.res-container {
  flex-wrap: wrap;
  justify-content: center;
  gap: 25px;
  padding: 20px;
  display: flex;
}

.res-card {
  background: #fff;
  border: none;
  border-radius: 15px;
  width: 280px;
  margin: 0;
  padding: 0;
  transition: all .3s;
  overflow: hidden;
  box-shadow: 0 2px 15px #00000014;
}

.res-card:hover {
  cursor: pointer;
  border: none;
  transform: translateY(-8px);
  box-shadow: 0 12px 35px #667eea4d;
}

.res-logo {
  object-fit: cover;
  border-radius: 0;
  width: 100%;
  height: 180px;
}

.res-card h3 {
  color: #333;
  margin: 15px 15px 8px;
  font-size: 18px;
  font-weight: 600;
}

.res-card h4 {
  color: #666;
  margin: 5px 15px;
  font-size: 14px;
  font-weight: 400;
}

.res-card h5 {
  color: #888;
  margin: 5px 15px 15px;
  font-size: 13px;
}

.footer {
  color: #fff;
  background: linear-gradient(135deg, #1e1e1e 0%, #2d2d2d 100%);
  margin-top: 60px;
  padding: 50px 20px 20px;
}

.footer-section h3 {
  color: #667eea;
  margin-bottom: 20px;
  font-size: 20px;
  font-weight: 600;
}

.footer-section ul li {
  color: #ccc;
  cursor: pointer;
  padding: 10px 0;
  transition: all .3s;
}

.footer-bottom {
  text-align: center;
  color: #999;
  border-top: 1px solid #444;
  margin-top: 50px;
  padding-top: 30px;
  font-size: 14px;
}

@media (width <= 768px) {
  .header {
    flex-direction: column;
    gap: 15px;
    padding: 15px 20px;
  }

  .nav-items > ul {
    flex-wrap: wrap;
    justify-content: center;
    font-size: 14px;
  }

  .nav-link {
    padding: 10px 15px;
  }

  .logo-text {
    font-size: 22px;
  }

  .search {
    flex-direction: column;
    padding: 15px;
  }

  .search-input, .search-btn, .reset-btn {
    width: 100%;
  }

  .footer-content {
    grid-template-columns: repeat(2, 1fr);
    gap: 30px;
  }

  .res-container {
    justify-content: center;
  }
}

@media (width <= 480px) {
  .footer-content {
    grid-template-columns: 1fr;
    gap: 25px;
  }

  .nav-items > ul {
    gap: 5px;
    font-size: 13px;
  }

  .nav-link {
    padding: 8px 12px;
  }

  .logo {
    width: 55px;
    height: 55px;
  }

  .logo-text {
    font-size: 20px;
  }
}

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
  background-color: #f5f7fa;
  font-family: Segoe UI, Tahoma, Geneva, Verdana, sans-serif;
}

.app {
  flex-direction: column;
  min-height: 100vh;
  display: flex;
}

.footer {
  color: #fff;
  background: linear-gradient(135deg, #1a1a2e 0%, #16213e 50%, #0f3460 100%);
  margin-top: 80px;
  padding: 60px 20px 20px;
  position: relative;
  overflow: hidden;
}

.footer:before {
  content: "";
  background: linear-gradient(90deg, #667eea 0%, #764ba2 50%, #f093fb 100%);
  height: 4px;
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
}

.footer-content {
  grid-template-columns: 2fr 1fr 1fr 1fr 1.5fr;
  gap: 50px;
  max-width: 1400px;
  margin: 0 auto;
  padding: 0 20px;
  display: grid;
}

.footer-section h3 {
  -webkit-text-fill-color: transparent;
  background: linear-gradient(135deg, #667eea 0%, #764ba2 100%) text;
  margin-bottom: 25px;
  font-size: 20px;
  font-weight: 700;
}

.footer-section.about h3 {
  margin-bottom: 15px;
  font-size: 28px;
}

.footer-description {
  color: #b8b9c1;
  margin-bottom: 25px;
  font-size: 15px;
  line-height: 1.8;
}

.social-links {
  gap: 15px;
  margin-top: 20px;
  display: flex;
}

.social-icon {
  cursor: pointer;
  background: #667eea1a;
  border: 2px solid #0000;
  border-radius: 50%;
  justify-content: center;
  align-items: center;
  width: 45px;
  height: 45px;
  font-size: 20px;
  transition: all .3s;
  display: flex;
}

.social-icon:hover {
  background: linear-gradient(135deg, #667eea 0%, #764ba2 100%);
  border-color: #fff;
  transform: translateY(-5px)scale(1.1);
  box-shadow: 0 8px 25px #667eea66;
}

.footer-section ul {
  margin: 0;
  padding: 0;
  list-style: none;
}

.footer-section ul li {
  color: #b8b9c1;
  cursor: pointer;
  padding: 12px 0;
  font-size: 15px;
  transition: all .3s;
  position: relative;
}

.footer-section ul li:hover {
  color: #667eea;
  padding-left: 8px;
}

.footer-section ul li:before {
  content: "";
  background: linear-gradient(90deg, #667eea, #764ba2);
  width: 0;
  height: 2px;
  transition: width .3s;
  position: absolute;
  top: 50%;
  left: 0;
  transform: translateY(-50%);
}

.footer-section ul li:hover:before {
  width: 0;
}

.cities ul {
  grid-template-columns: 1fr 1fr;
  gap: 8px;
  display: grid;
}

.view-all {
  text-align: center;
  background: #667eea1a;
  border-radius: 8px;
  grid-column: 1 / -1;
  margin-top: 10px;
  padding: 12px;
  font-weight: 600;
  color: #667eea !important;
}

.view-all:hover {
  background: #667eea33;
}

.footer-divider {
  background: linear-gradient(90deg, #0000, #667eea, #0000);
  max-width: 80%;
  height: 1px;
  margin: 50px auto;
}

.footer-bottom {
  margin-top: 30px;
}

.footer-bottom-content {
  flex-wrap: wrap;
  justify-content: space-between;
  align-items: center;
  gap: 20px;
  max-width: 1400px;
  margin: 0 auto;
  padding: 25px 20px;
  display: flex;
}

.footer-bottom p {
  color: #8b8d98;
  font-size: 14px;
}

.footer-badges {
  flex-wrap: wrap;
  gap: 15px;
  display: flex;
}

.badge {
  color: #b8b9c1;
  background: #667eea26;
  border: 1px solid #667eea4d;
  border-radius: 20px;
  padding: 8px 16px;
  font-size: 13px;
  transition: all .3s;
}

.badge:hover {
  color: #667eea;
  background: #667eea40;
  border-color: #667eea;
  transform: translateY(-2px);
}

@media (width <= 1200px) {
  .footer-content {
    grid-template-columns: 2fr 1fr 1fr;
    gap: 40px;
  }

  .cities {
    grid-column: 1 / -1;
  }

  .cities ul {
    grid-template-columns: repeat(4, 1fr);
  }
}

@media (width <= 768px) {
  .footer-content {
    grid-template-columns: 1fr 1fr;
    gap: 35px;
  }

  .footer-section.about, .cities {
    grid-column: 1 / -1;
  }

  .cities ul {
    grid-template-columns: repeat(3, 1fr);
  }

  .footer-bottom-content {
    text-align: center;
    flex-direction: column;
  }

  .footer-badges {
    justify-content: center;
  }
}

@media (width <= 480px) {
  .footer {
    padding: 40px 15px 15px;
  }

  .footer-content {
    grid-template-columns: 1fr;
    gap: 30px;
  }

  .cities ul {
    grid-template-columns: repeat(2, 1fr);
  }

  .social-links {
    justify-content: center;
  }

  .footer-badges {
    flex-direction: column;
    width: 100%;
  }

  .badge {
    text-align: center;
  }
}
